html{
    background-color: teal;
    width: 100%;
    height: 100%;
}

body{
    overflow: hidden;
    padding: 0px;
    margin: 0px;
    font-family: Arial, Helvetica, sans-serif;
    height: 100vh;
    display: flex;
    justify-content: center;
    transition: background-color 1000ms linear;
}

#root{
    display: flex;
    align-items: center;
    justify-content: center;
}

#App{
    color: white;
    margin: 20px;
    padding: 20px;
    padding-bottom: 10px;
    border-style: solid;
    word-wrap: break-word;
    border-radius: 5px;
    width: auto;
    max-width: 30vw;
}

@media screen and (max-width: 425px) {
    #App{
        border-style: solid;
        border-right: hidden;
        border-left: hidden;
        word-wrap: break-word;
        width: 100vw;
    }
}

.words{
    opacity: 1;
    animation-name: fade;
    animation-duration: 2s;
}

@keyframes fade{
    0% {opacity: 1;}
    50% {opacity: 0;}
    100% {opacity: 1;}
}

#Socials{
    display: flex;
    color: white
}

#tweet-quote{
    color:teal;
    text-decoration: none;
    transition: color 1000ms linear;
}

button{
    visibility: hidden;
    border-radius: 5px;
    border-style: none;
    color : teal;
    padding: 10px 20px;
    outline: none;
    transition: color 1000ms linear;
}

.fa-twitter{
    font-size: 30px;
    border-radius: 4px;
    padding: 10px;
    background: white;
}

#author{
    margin-right: 20px;
}

#clickables{
    display: flex;
    justify-content: space-between;
    text-align: left;
}
